<script>
import api from '@/api'
import { getToken } from "@/api/auth"

export default {
  data () {
      return {
        data: [],
        checkArrId1: [],
        checkArrId2: [],
        checkArrId3: [],
        checkArrId4: [],
      }
  },
  mounted () {
    const token = getToken(),
          grpid = this.$route.query.grpid*1,
          params = {"grpid": grpid, "token": token, "type": 1}

    api.authEdit(params).then(res => {
      if(res.code == 0) {
        this.data = res.data.models;
        this.data.forEach(ele1 => {
          ele1.check == 1 ? this.checkArrId1.push(`[${ele1.id}]`) : null;
          if(ele1.second&&ele1.second.length>0) {
            ele1.second.forEach(ele2 => {
              ele2.check == 1 ? this.checkArrId2.push(`[${ele2.id}]`) : null;
              if(ele2.third&&ele2.third.length>0) {
                ele2.third.forEach(ele3 => {
                  ele3.check == 1 ? this.checkArrId3.push(`[${ele3.id}]`) : null;
                  ele3.check1 == 1 ? this.checkArrId4.push(`[${ele3.id}][1]`) : null;
                  ele3.check2 == 1 ? this.checkArrId4.push(`[${ele3.id}][2]`) : null;
                  ele3.check3 == 1 ? this.checkArrId4.push(`[${ele3.id}][3]`) : null;
                  ele3.check4 == 1 ? this.checkArrId4.push(`[${ele3.id}][4]`) : null;
                })
              }
            })
          }
        })
        // console.log(this.checkArrId1,this.checkArrId2,this.checkArrId3,this.checkArrId4)
      }
    })
  },
  methods: {
		handleEdit () {
			console.log(this.checkArrId1)
			console.log(this.checkArrId2)
			console.log(this.checkArrId3)
			console.log(this.checkArrId4)
			// const checkArrId1 = this.checkArrId1;
			// const checkArrId2 = this.checkArrId1;
			// const checkArrId3 = this.checkArrId1;
			// const checkArrId4 = this.checkArrId1;
			// checkArrId1.forEach(ele => {
			// 	ele = 'first'+ele
			// })
			// checkArrId1.forEach(ele => {
			// 	ele = 'second'+ele
			// })
			// checkArrId1.forEach(ele => {
			// 	ele = 'first'+ele
			// })
			// checkArrId1.forEach(ele => {
			// 	ele = 'first'+ele
			// })
			// console.log(checkArrId1)
		},
    checkAllGroupChange1 (data) {
			const isChecked = this.checkArrId1.some(ele => {
				return ele == `[${data.itm_1.id}]`
			})
			// console.log(isChecked, data)
			isChecked ? this.checkedGroup1(data.itm_1) : this.uncheckedGroup1(data.itm_1)
    },
    checkAllGroupChange2 (data) {
      // console.log(data)
			const isChecked = this.checkArrId2.some(ele => {
				return ele == `[${data.itm_2.id}]`
			})
			// console.log(isChecked, data)
			isChecked ? (this.checkedGroup2(data.itm_2),this.hasCheckedChildren1(data.itm_1)) : (this.uncheckedGroup2(data.itm_2),this.isHasCheckedChildren1(data.itm_1))
    },
    checkAllGroupChange3 (data) {
      // console.log(data)
			const isChecked = this.checkArrId3.some(ele => {
				return ele == `[${data.itm_3.id}]`
			})
			const isChecked1 = this.checkArrId4.some(itm => {
				return itm == `[${data.itm_3.id}][1]`
			})
			const isChecked2 = this.checkArrId4.some(itm => {
				return itm == `[${data.itm_3.id}][2]`
			})
			const isChecked3 = this.checkArrId4.some(itm => {
				return itm == `[${data.itm_3.id}][3]`
			})
			const isChecked4 = this.checkArrId4.some(itm => {
				return itm == `[${data.itm_3.id}][4]`
			})
			isChecked ? null : this.checkArrId3.push(`[${data.id}]`)
			if(isChecked) {
				isChecked1 ? null : this.checkArrId4.push(`[${data.itm_3.id}][1]`)
				isChecked2 ? null : this.checkArrId4.push(`[${data.itm_3.id}][2]`)
				isChecked3 ? null : this.checkArrId4.push(`[${data.itm_3.id}][3]`)
				isChecked4 ? null : this.checkArrId4.push(`[${data.itm_3.id}][4]`)
				// console.log(this.checkArrId4)
				//祖系状态判断
				this.hasCheckedChildren2(data.itm_2)
				this.hasCheckedChildren1(data.itm_1)
			} else {
				!isChecked ? null : this.checkArrId3.splice(this.checkArrId3.indexOf(`[${data.itm_3.id}]`), 1)
				!isChecked1 ? null : this.checkArrId4.splice(this.checkArrId4.indexOf(`[${data.itm_3.id}][1]`), 1)
				!isChecked2 ? null : this.checkArrId4.splice(this.checkArrId4.indexOf(`[${data.itm_3.id}][2]`), 1)
				!isChecked3 ? null : this.checkArrId4.splice(this.checkArrId4.indexOf(`[${data.itm_3.id}][3]`), 1)
				!isChecked4 ? null : this.checkArrId4.splice(this.checkArrId4.indexOf(`[${data.itm_3.id}][4]`), 1)
				// console.log(this.checkArrId4)
				//祖系状态判断
				this.isHasCheckedChildren2(data.itm_2)
				this.isHasCheckedChildren1(data.itm_1)
			}
    },
    checkAllGroupChange4 (data) {
      // console.log(data)
			const isChecked1 = this.checkArrId4.some(itm => {
				return itm == `[${data.itm_3.id}][1]`
			})
			const isChecked2 = this.checkArrId4.some(itm => {
				return itm == `[${data.itm_3.id}][2]`
			})
			const isChecked3 = this.checkArrId4.some(itm => {
				return itm == `[${data.itm_3.id}][3]`
			})
			const isChecked4 = this.checkArrId4.some(itm => {
				return itm == `[${data.itm_3.id}][4]`
			})
			if(isChecked1 || isChecked2 || isChecked3 || isChecked4) {
				this.hasCheckedChildren3(data.itm_3)
				this.hasCheckedChildren2(data.itm_2)
				this.hasCheckedChildren1(data.itm_1)
			} else {
				this.isHasCheckedChildren3(data.itm_3)
				this.isHasCheckedChildren2(data.itm_2)
				this.isHasCheckedChildren1(data.itm_1)
			}
    },
		checkedGroup1 (data) {
			if(data.second&&data.second.length>0) {
				data.second.forEach(ele1 => {
					//二级菜单判断是否选中
					const isChecked1 = this.checkArrId2.some(itm => {
						return itm == `[${ele1.id}]`
					})
					// console.log(isChecked1)
					isChecked1 ? null : this.checkArrId2.push(`[${ele1.id}]`)
					//三级菜单判断是否选中
					if(ele1.third&&ele1.third.length>0) {
						ele1.third.forEach(ele2 => {
							const isChecked2 = this.checkArrId3.some(itm => {
								return itm == `[${ele2.id}]`
							})
							const isChecked21 = this.checkArrId4.some(itm => {
								return itm == `[${ele2.id}][1]`
							})
							const isChecked22 = this.checkArrId4.some(itm => {
								return itm == `[${ele2.id}][2]`
							})
							const isChecked23 = this.checkArrId4.some(itm => {
								return itm == `[${ele2.id}][3]`
							})
							const isChecked24 = this.checkArrId4.some(itm => {
								return itm == `[${ele2.id}][4]`
							})
							// console.log(isChecked2)
							isChecked2 ? null : this.checkArrId3.push(`[${ele2.id}]`)
							isChecked21 ? null : this.checkArrId4.push(`[${ele2.id}][1]`)
							isChecked22 ? null : this.checkArrId4.push(`[${ele2.id}][2]`)
							isChecked23 ? null : this.checkArrId4.push(`[${ele2.id}][3]`)
							isChecked24 ? null : this.checkArrId4.push(`[${ele2.id}][4]`)
						})
						// console.log(this.checkArrId4)
					}
				})
			}
		},
		uncheckedGroup1 (data) {
			if(data.second&&data.second.length>0) {
				data.second.forEach(ele1 => {
					//二级菜单判断是否选中
					const isChecked1 = this.checkArrId2.some(itm => {
						return itm == `[${ele1.id}]`
					})
					// console.log(isChecked1)
					!isChecked1 ? null : this.checkArrId2.splice(this.checkArrId2.indexOf(`[${ele1.id}]`),1)
					//三级菜单判断是否选中
					if(ele1.third&&ele1.third.length>0) {
						ele1.third.forEach(ele2 => {
							const isChecked2 = this.checkArrId3.some(itm => {
								return itm == `[${ele2.id}]`
							})
							const isChecked21 = this.checkArrId4.some(itm => {
								return itm == `[${ele2.id}][1]`
							})
							const isChecked22 = this.checkArrId4.some(itm => {
								return itm == `[${ele2.id}][2]`
							})
							const isChecked23 = this.checkArrId4.some(itm => {
								return itm == `[${ele2.id}][3]`
							})
							const isChecked24 = this.checkArrId4.some(itm => {
								return itm == `[${ele2.id}][4]`
							})
							// console.log(isChecked2)
							!isChecked2 ? null : this.checkArrId3.splice(this.checkArrId3.indexOf(`[${ele2.id}]`), 1)
							!isChecked21 ? null : this.checkArrId4.splice(this.checkArrId4.indexOf(`[${ele2.id}][1]`), 1)
							!isChecked22 ? null : this.checkArrId4.splice(this.checkArrId4.indexOf(`[${ele2.id}][2]`), 1)
							!isChecked23 ? null : this.checkArrId4.splice(this.checkArrId4.indexOf(`[${ele2.id}][3]`), 1)
							!isChecked24 ? null : this.checkArrId4.splice(this.checkArrId4.indexOf(`[${ele2.id}][4]`), 1)
						})
						// console.log(this.checkArrId4)
					}
				})
			}
		},
		checkedGroup2 (data) {
			//三级菜单判断是否选中
			if(data.third&&data.third.length>0) {
				data.third.forEach(ele => {
					const isChecked = this.checkArrId3.some(itm => {
						return itm == `[${ele.id}]`
					})
					const isChecked1 = this.checkArrId4.some(itm => {
						return itm == `[${ele.id}][1]`
					})
					const isChecked2 = this.checkArrId4.some(itm => {
						return itm == `[${ele.id}][2]`
					})
					const isChecked3 = this.checkArrId4.some(itm => {
						return itm == `[${ele.id}][3]`
					})
					const isChecked4 = this.checkArrId4.some(itm => {
						return itm == `[${ele.id}][4]`
					})
					// console.log(isChecked2)
					isChecked ? null : this.checkArrId3.push(`[${ele.id}]`)
					isChecked1 ? null : this.checkArrId4.push(`[${ele.id}][1]`)
					isChecked2 ? null : this.checkArrId4.push(`[${ele.id}][2]`)
					isChecked3 ? null : this.checkArrId4.push(`[${ele.id}][3]`)
					isChecked4 ? null : this.checkArrId4.push(`[${ele.id}][4]`)
				})
				// console.log(this.checkArrId4)
			}
		},
		uncheckedGroup2 (data) {
			//三级菜单判断是否选中
			if(data.third&&data.third.length>0) {
				data.third.forEach(ele => {
					const isChecked = this.checkArrId3.some(itm => {
						return itm == `[${ele.id}]`
					})
					const isChecked1 = this.checkArrId4.some(itm => {
						return itm == `[${ele.id}][1]`
					})
					const isChecked2 = this.checkArrId4.some(itm => {
						return itm == `[${ele.id}][2]`
					})
					const isChecked3 = this.checkArrId4.some(itm => {
						return itm == `[${ele.id}][3]`
					})
					const isChecked4 = this.checkArrId4.some(itm => {
						return itm == `[${ele.id}][4]`
					})
					!isChecked ? null : this.checkArrId3.splice(this.checkArrId3.indexOf(`[${ele.id}]`), 1)
					!isChecked1 ? null : this.checkArrId4.splice(this.checkArrId4.indexOf(`[${ele.id}][1]`), 1)
					!isChecked2 ? null : this.checkArrId4.splice(this.checkArrId4.indexOf(`[${ele.id}][2]`), 1)
					!isChecked3 ? null : this.checkArrId4.splice(this.checkArrId4.indexOf(`[${ele.id}][3]`), 1)
					!isChecked4 ? null : this.checkArrId4.splice(this.checkArrId4.indexOf(`[${ele.id}][4]`), 1)
					// console.log(this.checkArrId3,this.checkArrId4)
				})
			}
		},
		hasCheckedChildren1 (data) {
			const isChecked = this.checkArrId1.some(ele => {
				return ele == `[${data.id}]`
			})
			!isChecked ? this.checkArrId1.push(`[${data.id}]`) : null;
		},
		isHasCheckedChildren1 (data) {
			// console.log(data)
			if(data.second&&data.second.length>0) {
				let isHave = false;
				for(let i=0;i<data.second.length;i++) {
					isHave = this.checkArrId2.some(itm => {
						return itm == `[${data.second[i].id}]`
					})
					if(isHave) break;
				}
				
				if(isHave) {
					this.hasCheckedChildren1(data)
				} else {
					const isChecked = this.checkArrId1.some(ele => {
						return ele == `[${data.id}]`
					})
					isChecked ? this.checkArrId1.splice(this.checkArrId1.indexOf(`[${data.id}]`),1) : null;
				}
			}
		},
		hasCheckedChildren2 (data) {
			const isChecked = this.checkArrId2.some(ele => {
				return ele == `[${data.id}]`
			})
			!isChecked ? this.checkArrId2.push(`[${data.id}]`) : null;
		},
		isHasCheckedChildren2 (data) {
			// console.log(data)
			if(data.third&&data.third.length>0) {
				let isHave = false;
				for(let i=0;i<data.third.length;i++) {
					isHave = this.checkArrId3.some(itm => {
						return itm == `[${data.third[i].id}]`
					})
					if(isHave) break;
				}
				// console.log(isHave)
				if(isHave) {
					this.hasCheckedChildren2(data)
				} else {
					const isChecked = this.checkArrId2.some(ele => {
						return ele == `[${data.id}]`
					})
					isChecked ? this.checkArrId2.splice(this.checkArrId2.indexOf(`[${data.id}]`),1) : null;
				}
			}
		},
		hasCheckedChildren3 (data) {
			const isChecked = this.checkArrId3.some(ele => {
				return ele == `[${data.id}]`
			})
			!isChecked ? this.checkArrId3.push(`[${data.id}]`) : null;
		},
		isHasCheckedChildren3 (data) {
			// console.log(data)
			const isChecked = this.checkArrId3.some(ele => {
				return ele == `[${data.id}]`
			})
			isChecked ? this.checkArrId3.splice(this.checkArrId3.indexOf(`[${data.id}]`),1) : null;
		},
  }
}
</script>
<template>
  <div>
    <div v-for="itm_1,index_1 in data" class="menu_first" :key="index_1">
      <div class="menu_first_head">
        <CheckboxGroup v-model="checkArrId1" @on-change="checkAllGroupChange1({itm_1})">
          <Checkbox
            :label="'['+itm_1.id+']'"
            >{{itm_1.name}}</Checkbox>
        </CheckboxGroup>
      </div>
      <div v-for="itm_2,index_2 in itm_1.second" class="menu_second" :key="index_2">
        <div class="menu_second_head">
          <CheckboxGroup v-model="checkArrId2" @on-change="checkAllGroupChange2({itm_1,itm_2})">
            <Checkbox
              :label="('['+itm_2.id+']')"
              >{{itm_2.name}}</Checkbox>
              <!-- @click.prevent.native="handleCheckAll(itm_1,itm_2)" -->
          </CheckboxGroup> 
        </div>
        <div v-for="itm_3,index_3 in itm_2.third" class="menu_third" :key="index_3">
          <CheckboxGroup v-model="checkArrId3" @on-change="checkAllGroupChange3({itm_1,itm_2,itm_3})">
            <Checkbox
              :label="('['+itm_3.id+']')"
              >{{itm_3.name}}
              <!-- @click.prevent.native="handleCheckAll(itm_1, itm_2, itm_3,$event)" -->
            </Checkbox>
          </CheckboxGroup>
          <CheckboxGroup v-model="checkArrId4" @on-change="checkAllGroupChange4({itm_1,itm_2,itm_3})">
            <Checkbox :label="'['+itm_3.id+'][1]'">增</Checkbox>
            <Checkbox :label="'['+itm_3.id+'][2]'">删</Checkbox>
            <Checkbox :label="'['+itm_3.id+'][3]'">改</Checkbox>
            <Checkbox :label="'['+itm_3.id+'][4]'">查</Checkbox>
          </CheckboxGroup>
        </div>
      </div>
    </div>
		<Button type="info" ghost @click="handleEdit" style="margin-left:20px;width:80px">提交</Button>
    <Button type="success" to='/admin/adminGroup' ghost style="margin-left:20px;width:80px">取消</Button>
  </div>
</template>
<style lang="scss" scoped>
.menu_first {
  overflow: hidden;
  // border: 1px solid #ccc;
  padding: 0 20px;
  margin: 0 20px;
  .menu_first_head {
    border-bottom: 1px solid #ccc;
    padding: 20px 0 20px 0;
  }
  .menu_second {
    // float: left;
    overflow: hidden;
    border-bottom: 1px dashed #ccc;
    padding: 0 0 20px 20px;
    margin: 0 20px 20px 0;
    .menu_second_head {
      padding: 20px;
    }
    .menu_third {
      float: left;
      padding: 20px;
      border-top: 1px dashed #ccc;
      border-right: 1px dashed #ccc;
      border-radius: 4px;
      margin-right: 20px;
      >.ivu-checkbox-group>label {
        margin-bottom: 10px;
      }
    }
  }
}
</style>
